.App {
  padding: 30px;
  text-align: center;
  background: cornsilk;
  height: 150px;
  width: 150px;
  display: flex;
  overflow: scroll;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
  overflow-x: hidden;
}
.App::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 10px;
}

.App::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.5);
  -webkit-box-shadow: 0 0 1px rgba(255, 255, 255, 0.5);
}

.App::-webkit-scrollbar {
  width: 10px;
  -webkit-appearance: none;
}

.App::-webkit-scrollbar-track {
  background: yellow;
  border-radius: 8px;
}

.App::-webkit-scrollbar-thumb {
  background: red;
  border-radius: 8px;
}

.App::-webkit-scrollbar-thumb:hover {
  background: #355cc9;
}
